import React from 'react';
import classes  from './Post.module.css'

const Post = (props) => {

  return (
    <div className={classes.item}>
      <img  alt='' src='data:image/jpeg;base64,'></img>
      {props.message}
      <div>
        <span>{props.count}</span>
      </div>
    </div>
  )
}

export default Post;